@import url(https://fonts.googleapis.com/css?family=Rajdhani:700);
.content{
    text-align: center;
}

.introduce{
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.f_menu{
    text-align: center;
    padding-bottom: 2px;
    min-height: 25px;
    
    background-color: lightgrey;
    color: black;
}

.f_content{
    text-align: center;
    min-height: 80px;
    
    color: gray;
    vertical-align: middle;
}
.Positioner{
    display: flex;
    flex-direction: column;
    
    top: 0px;
    width: 100%;
    box-shadow: gray;
}

/* 흰 배경, 내용 중간 정렬 */
.WhiteBackground {
    background: white;
    
    justify-content: center;
    height: auto;
}

/* 해더의 내용 */
.HeaderContents {
    width: 1200px;
    height: 55px;
    display: flex;
    flex-direction: row;
    align-items: center;

    padding-right: 1rem;
    padding-left: 1rem;
}

/*로고*/
.Logo{
    font-size: 1.4rem;
    letter-spacing: 2px;
    color: teal;
    font-family: "고딕";
}

/* 하단 그래디언트 테두리*/
.GradientBorder {
    height: 3px;
    background: linear-gradient(to right, teal, cyan);
}

.BorderedButton{
    background-color: white;
    font-weight: 600;
    color: teal;
    border: 1px solid;
    border-color: teal;
    padding: 0.5rem;
    padding-bottom: 0.4rem;
    cursor: pointer;
    border-radius: 2px;
    text-decoration: none;
    transition: .2s all; 
}

.BorderedButton:hover {
    background: teal;
    color: white;
    box-shadow: gray;
}

.BorderedButton:active {
    /* 마우스 클릭시 아래로 미세하게 움직임 */
    transform: translateY(3px);
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  box-sizing: border-box;
  background: #f1f1f5;
}

*, *:before, *:after {
  box-sizing: inherit;
}
